import { EVENTS_ADD_EVENT, EVENTS_CATEGORY, EVENTS_TAGS, UPLOAD_BY_ALI_OSS, UPLOAD_BY_SERVICE, GET_COUNTRY_LIST, USER_OBJECT_LIST, EVENTS_DETAIL } from '@/api/api';
